.form {
    position: relative;
}

.item {
    display: grid;
    grid-template-columns: 1fr 5fr;
    align-items: center;
    margin: 0;
}

.char {
    color: firebrick;
    font-weight: bold;
    text-shadow: 2px 2px 2px black;
}

.charValue {
    text-shadow: 2px 2px 0 black;
}

.button {
    position: absolute;
    right: 0;
    top: -30px;
}